import React from "react";

export default function CTA({handleSubmit}) {
    let contactsRef = React.createRef();
    return(
        <div className="cta">
            <div className="cta-title">     </div>
            <div className="cta-input-wrapper">
                <input type="text" name="email" ref={contactsRef} placeholder=" "/>
                <button className="offer-callback-btn" onClick={(e) => handleSubmit(contactsRef)}> </button>
            </div>
        </div>
    )
}
